.btn {
    display: inline-block;
    line-height: 50px;
    height: 50px;
    text-align: center;
    width: 100px;
    cursor: pointer;
    vertical-align: center;
    margin-right: 10px;
}

.btnThree {
    color: #FFF;
    transition: all 0.5s;
    position: relative;
}
.btnThree::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(255,255,255,0.1);
    transition: all 0.3s;
}
.btnThree:hover::before {
    opacity: 0 ;
    transform: scale(0.5,0.5);
}
.btnThree::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all 0.3s;
    border: 1px solid rgba(255,255,255,0.5);
    transform: scale(1.2,1.2);
}
.btnThree:hover::after {
    opacity: 1;
    transform: scale(1,1);
}

